<template>
  <div id="batchlmport">
    <!--  -->
    <Breadcrumbs :items="items" />

    <v-stepper v-model="e6" vertical>
      <v-stepper-step :complete="e6 > 1" step="1">
        Excel(.xlsx)
        <small>1000(27kb)</small>
      </v-stepper-step>

      <v-stepper-content step="1">
        <v-card color="grey lighten-1" class="mb-5" height="190px">
          <div class="app-container">
            <!-- // excel -->
            <Updata :on-success="handleSuccess" :before-upload="beforeUpload" />
            <!-- <v-btn flat @click="download" ></v-btn> -->

            <!-- <upload-excel-component :on-success="handleSuccess" :before-upload="beforeUpload"/> -->
            <!-- // excel -->
            <!-- <div
              :data="tableData"
              border
              highlight-current-row
              style="width: 100%;margin-top:20px;"
            >-->

            <!-- </div> -->
          </div>
          <!-- ---------------------  -->
        </v-card>
        <!-- <v-btn  color="primary" ></v-btn> -->
        <v-btn flat></v-btn>
      </v-stepper-content>

      <v-stepper-step :complete="e6 > 2" step="2"></v-stepper-step>

      <v-stepper-content step="2">
        <v-card color="grey lighten-1" class="mb-5" height="600px">
          <code v-for="(item,index) of tableData" :prop="item" :label="item" :key="index">{{ item }}</code>
        </v-card>
        <v-btn color="primary" @click="e6 = 3"></v-btn>
        <!-- <v-btn flat>Cancel</v-btn> -->
      </v-stepper-content>

      <v-stepper-step :complete="e6 > 3" step="3">,</v-stepper-step>

      <v-stepper-content step="3">
        <v-card color="grey lighten-1" class="mb-5" height="auto">
          <v-data-table
            :items="tableData"
            :headers="headers"
            class="elevation-1"
            :dark="true"
            :rows-per-page-text="``"
            :rows-per-page-items="pagingList"
          >
            <template v-slot:items="props">
              <!-- <td>{{ props.item.name }}</td> -->
              <td class="text-xs-centre">{{ props.item[''] }}</td>
              <td class="text-xs-centre">{{ props.item['SN'] }}</td>
              <td class="text-xs-centre">{{ props.item['SN'] }}</td>
              <td class="text-xs-centre">{{ props.item[''] }}</td>
              <td class="text-xs-centre">
                <v-btn flat small color="error" @click="del(props.index)"></v-btn>
              </td>
            </template>
          </v-data-table>
        </v-card>
        <v-btn color="primary" @click="upData"></v-btn>
        <!-- <v-btn flat>Cancel</v-btn> -->
      </v-stepper-content>

      <v-stepper-step step="4"></v-stepper-step>
      <v-stepper-content step="4">
        <v-card color="grey lighten-1" class="mb-5" height="200px">
          <div>
            <!-- <input
              ref="excel-upload-input"
              class="excel-upload-input"
              type="file"
              accept=".xlsx, .xls"
              @change="handleClick"
            >-->
            <div class="drop">
              
              <v-btn flat color="primary" to="/deviceList"></v-btn>
              <!-- <v-btn
                color="info"
                :loading="loading"
                style="margin-left:16px;"
                size="mini"
                type="primary"
                @click="handleUpload"
              ></v-btn>-->

              <!-- <v-btn :loading="loading" @click="alert.snackbar = true" href="./test.xlsx" download=".xlsx" ></v-btn> -->
            </div>
          </div>
        </v-card>
        <v-btn color="primary" @click="e6 = 1"></v-btn>
        <!-- <v-btn flat>Cancel</v-btn> -->
      </v-stepper-content>
    </v-stepper>
    <!--  -->
    <v-dialog v-model="del_dialog" hide-overlay persistent width="300">
      <v-card color="primary" dark>
        <v-card-text>
          ...
          <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
// import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
import Breadcrumbs from "../../breadcrumbs/breadcrumbs";
import Updata from "./updata.vue";
import { setTimeout } from "timers";
export default {
  components: {
    Breadcrumbs,
    Updata
  },

  data() {
    return {
      // 
      tableData: [],
      tableHeader: [],
      // 
      pagingList: [10, 20, 30, 40, 50, { text: "100", value: 100 }],
      e6: 1,
      del_dialog: false,
      items: [
        {
          text: "",
          disabled: false,
          to: "/deviceList"
        },
        {
          text: "",
          disabled: false,
          to: "/batchImport"
        }
      ],
      headers: [
        {
          text: "",
          align: "center",
          sortable: false,
          value: "name"
        },
        {
          text: "sn",
          value: "calories",
          sortable: false,
          align: "center"
        },
        { text: "sn", value: "fat", sortable: false, align: "center" },
        { text: "", value: "carbs", sortable: false, align: "center" },
        { text: "", value: "carbs", sortable: false, align: "center" }
      ]
    };
  },
  methods: {
    // 
    beforeUpload(file) {
      // 1mb
      const isLt1M = file.size / 1024 / 1024 < 1;
      if (isLt1M) {
        return true;
      }
      this.$message({
        message: "Excel1mb.",
        type: "warning"
      });
      return false;
    },
    // 
    handleSuccess({ results, header }) {
      this.tableData = results;
      this.tableHeader = header;
      this.e6 = 2;
    },
    // 
    download() {
      let url = "../../../assets/download/test.xlsx";
      window.location.href = url;
    },
    // 
    del(index) {
      if (index <= this.tableData.length) {
        this.tableData.splice(index, 1);
      }
    },
    // 
    trim(s) {
      return s.replace(/(^\s*)|(\s*$)/g, "");
    },
    // 
    upData() {
      let deviceList = [];
      this.tableData.forEach(item => {
        let list = {
          agent_name:
           this.trim(item[""] == undefined ? "" : item[""]) ,
          base_below_sn:
            this.trim(item["SN"] == undefined ? "" : item["SN"]),
          base_above_sn:
            this.trim(item["SN"] == undefined ? "" : item["SN"]),
          blue_tooth: this.trim(item[""] == undefined ? "" : item[""])
        };
        deviceList.push(list);
      });
      this.del_dialog = true;

      this.$axios({
        method: "post",
        url: this.$apis.URL.device_update_sn,
        data: {
          data: deviceList
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.desserts = response.data.datas.list;
            this.$Dialog.getProductCheckResult_({
                text: "",
                timeout: 3000,
                snackbar: true
            })
            this.e6 = 4;
          } else {
            if( response.data.message == "not found" ){
              this.$Dialog.getProductCheckResult_({
                text: "" + "",
                timeout: 3000,
                snackbar: true
            })
            }else{
              this.$Dialog.getProductCheckResult_({
                text: response.data.message,
                timeout: 3000,
                snackbar: true
            })
            }
          }
          setTimeout(() => {
            this.del_dialog = false;
          }, 500);
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({
                text: error,
                timeout: 3000,
                snackbar: true
            })
        });
    }
  }
};
</script>

<style lang="less" scoped>
#batchlmport {
  .mb-5 {
    overflow-y: auto;
    text-align: center;
    padding: 15px 0;
  }
  code {
    text-align: left;
    display: block;
    padding-left: 50px;
  }
}
</style>
